@lost flexbox flex;
* {
  box-sizing: border-box;
}
article,
details,
section,
summary,
aside,
main,
menu,
nav,
figcaption,
figure,
footer,
header,
hgroup {
  display: block;
}
audio,
canvas,
progress,
video {
  display: inline-block;
  vertical-align: baseline;
}
audio:not([controls]) {
  display: none;
  height: 0;
}
[hidden],
template {
  display: none;
}
.btn {
  -webkit-border-radius: 22px;
  border-radius: 22px;
  color: #fff;
  display: inline-block;
  font-weight: 400;
  line-height: 44px;
  text-decoration: none;
  width: 162px;
}
.btn__green {
  background: #3dd2ae;
  font-size: 14px;
  padding: 0 50px;
}
body {
  font-family: 'Open Sans';
  margin: 0;
}
header {
  background: url("../img/top-bg.jpg") no-repeat center center;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  background-size: cover;
  lost-utility: clearfix;
}
header section {
  zoom: 1;
}
header section:before,
header section:after {
  content: "";
  display: table;
}
header section:after {
  clear: both;
}
header .top {
  padding: 46px 70px 0 70px;
  lost-column: 1/1;
}
header .top .top__logo {
  float: left;
}
