  background: #343c48;
  border: none;
  -webkit-border-radius: 22px;
  border-radius: 22px;
  color: #9da5b0;
  font-size: 14px;
  height: 44px;
  line-height: 44px;
  padding: 0 40px 0 20px;
  position: absolute;
  top: 0;
  right: 0;
  width: 262px;
}
header .top .top__right .top__right__left:focus {
  outline: none;
}
header .top .top__right .top__right__left i {
  color: #b2b2b2;
  position: absolute;
  top: 13px;
  right: 19px;
}
